.default {

    display: inline-block;
    width: 80px;
    height: 25px;
    line-height: 25px;
    border-radius: 45px;
    margin: 10px 20px;
    font-family: 'Montserrat', sans-serif;
    font-size: 8px;
    text-transform: uppercase;
    text-align: center;
    letter-spacing: 3px;
    font-weight: 600;
    color: #524f4e;
    background: rgba(84, 202, 253, 0.51);
    box-shadow: 0 15px 20px rgba(46, 229, 157, .4);
    transition: .3s;
}
.default:hover {
     background: #3761fd;
     box-shadow: 0 15px 20px rgba(46, 229, 157, .4);
     color: white;
     transform: translateY(-7px);
 }
.default:disabled {
    background: #797979;
    box-shadow: 0 15px 20px rgba(205, 245, 229, 0.4);
    color: white;
    transform: translateY(-7px);
}

.red {
    width: 80px;
    height: 25px;
    line-height: 25px;
    border-radius: 45px;
    margin: 10px 20px;
    font-family: 'Montserrat', sans-serif;
    font-size: 11px;
    text-transform: uppercase;
    text-align: center;
    letter-spacing: 3px;
    font-weight: 600;
    color: #524f4e;
    background: red;
    box-shadow: 0 8px 15px rgba(0, 0, 0, .1);
    transition: .3s;
}